.topnav {
  overflow: hidden;
}

.topnav navlink {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav navlink:hover {
  background-color: #ddd;
  color: black;
}

.topnav navlink.active {
 
  color: white;
}

.topnav .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .topnav navlink:not(:first-child) {display: none;}
  .topnav navlink.icon {
    float: right;
    display: block;
  }
  PlanetFirstLogo{
    width:100%;
    float:left;
    display: block;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive navlink {
    float: none;
    display: block;
    text-align: left;
  }
}
